.Modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.ModalContent {
  width: min-content;
  background-color: white;
  padding: 30px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-wrap: wrap;
  position: relative;
  justify-content: center;
}

.ModalClose {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  font-size: 32px;
  color: rgba(209, 209, 214, 1);
}
